* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell,
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}
.switcher-box {
  text-align: center;
  margin-top: 200px;
}
.switcher-btn {
  width: 150px;
  height: 50px;
  margin: 5px;
  font-size: 25px;
  text-transform: uppercase;
}

.timer-input,
.timer-btn {
  height: 40px;
  min-width: 80px;
}

.timer {
  display: flex;
  justify-content: space-between;
  padding: 50px 200px;
}

.field {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.value {
  font-size: 130px;
}

.label {
  font-size: 50px;
}
